import { mapGetters } from 'vuex';
import axios from 'axios';
import $http from '@/api';

const SET_ALL_LINKS = 'SET_ALL_LINKS';

export default {
  name: 'Links',
  data() {
    return {
      addLinkForm: {
        original: '',
        short: '',
        type_id: '1',
      },
    };
  },
  methods: {
    addLink_all(payload) {
       $http.post('/add-link', payload)
        .then((res) => {
          this.message = res.data.short_link;
          this.addLinkForm.short = res.data.short_link;
          this.showMessage = true;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    initForm() {
      this.addLinkForm.original = '';
      this.addLinkForm.short = '';
      this.addLinkForm.type_id = '1';
    },
    onSubmit() {
      const payload = {
        original: this.addLinkForm.original,
        type_id: 1,
      };
      this.addLink_all(payload);
      this.initForm();
    },
   },
};